
html,
body {
  height: 100%;
}
#app {
  min-height: 100%;
  margin: 0;
  padding: 0;
  position: relative;
}
.container {
  margin: 48px auto;
  width: 90%;
  background-color: #fff;
  padding-bottom: 65px;
}
.container .nav {
  float: left;
  width: 210px;
}
.container .view {
  float: left;
  width: calc(100% - 215px);
  padding: 32px 48px 48px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.container .view h1 {
  font-size: 2em;
}
.container .view h2 {
  font-size: 1.5em;
  color: #1f2d3d;
  margin: 0;
}
.container .view h3 {
  font-size: 1.25rm;
}
.container .view h2,
.container .view h3,
.container .view h4,
.container .view h5 {
  font-weight: normal;
  color: #1f2f3d;
}
.container .view h2:hover a,
.container .view h3:hover a,
.container .view h4:hover a,
.container .view h5:hover a {
  opacity: .4;
}
.container .view h2 a,
.container .view h3 a,
.container .view h4 a,
.container .view h5 a {
  float: left;
  margin-left: -20px;
  opacity: 0;
  cursor: pointer;
}
.container .view h2 a:hover,
.container .view h3 a:hover,
.container .view h4 a:hover,
.container .view h5 a:hover {
  opacity: .4;
}
.container .view p {
  font-size: 14px;
  color: #5e6d82;
  line-height: 1.5em;
}
.container .view ul {
  padding-left: 40px;
  list-style-type: disc;
}
.container .view ul li {
  margin-bottom: 5px;
  font-size: 14px;
}
.container .view .code_inline {
  padding: 0;
  padding-top: .2em;
  padding-bottom: .2em;
  margin: 0;
  font-size: 85%;
  background-color: rgba(27, 31, 35, 0.05);
  border-radius: 3px;
}
.container .demo {
  margin: 20px 0;
}
.container:after {
  content: "";
  clear: both;
  display: block;
}

.page-header {
  background-color: #fff;
  -webkit-box-shadow: 0 10px 60px 0 rgba(29, 29, 31, 0.07);
          box-shadow: 0 10px 60px 0 rgba(29, 29, 31, 0.07);
  opacity: 0.98;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  -webkit-transition: all .3s;
  transition: all .3s;
  z-index: 100;
}
.page-header .header-container {
  width: 90%;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 80px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.page-footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 60px;
  padding: 30px 0 30px;
  color: #495060;
  text-align: center;
  background-color: #fff;
  -webkit-box-shadow: 0 4px 30px 0 rgba(223, 225, 230, 0.8);
          box-shadow: 0 4px 30px 0 rgba(223, 225, 230, 0.8);
}
.page-footer .footer-container {
  width: 90%;
  margin: 0 auto;
}

.side-nav {
  display: inline-block;
  margin: 32px 0;
  padding: 0;
  color: #3F536E;
  background-color: #fff;
  z-index: 99;
}
.side-nav .group-container {
  margin-bottom: 32px;
}
.side-nav .side-nav-title {
  padding: 0 24px 8px;
  color: #8DABC4;
  font-size: 12px;
  font-weight: bold;
  letter-spacing: 1px;
  text-transform: uppercase;
}
.side-nav .side-nav-items {
  font-size: 14px;
  font-weight: normal;
  line-height: 1.8;
}
.side-nav .side-nav-items a {
  display: block;
  position: relative;
  padding: 8px 24px;
  color: #3F536E;
  font-weight: normal;
  line-height: 1.5;
  cursor: pointer;
}
.side-nav .side-nav-items .side-nav-group {
  display: block;
  position: relative;
  padding: 6px 0 6px 24px;
  color: #2C405A;
  font-weight: bold;
}
.side-nav .side-nav-items .slid-nav-component {
  display: block;
  position: relative;
  padding: 6px 24px 6px 32px;
  color: #616367;
  font-size: 14px;
}
.side-nav .side-nav-items .active {
  color: #3FAAF5;
}

.pg-index .index-container {
  width: 960px;
  margin: 0 auto;
  text-align: center;
  padding: 100px 0;
}
.pg-index .index-container p {
  margin-top: 56px;
  color: #8994C6;
  font-size: 32px;
}
.pg-index .separate-panel {
  position: relative;
}
.pg-index .separate-panel .about {
  position: absolute;
  width: 100%;
  height: 200px;
  top: 20px;
  z-index: 9;
  background: #fff;
  text-align: center;
}
.pg-index .separate-panel .about a {
  color: #fff;
  background-image: -webkit-gradient(linear, left top, right top, from(#009b72), to(#009b72));
  background-image: linear-gradient(90deg, #009b72 0%, #009b72 100%);
  -webkit-box-shadow: 0 2px 6px 0 rgba(51, 105, 231, 0.4);
          box-shadow: 0 2px 6px 0 rgba(51, 105, 231, 0.4);
  display: inline-block;
  width: 190px;
  height: 56px;
  margin: 0 35px;
  font-size: 18px;
  line-height: 56px;
  border-radius: 28px;
  -webkit-transition: all .3s;
  transition: all .3s;
  cursor: pointer;
}
.pg-index .separate-panel .about .btn-github {
  color: #316DE9;
  border: 1px solid #2F6EE9;
  background: #fff;
  -webkit-box-shadow: none;
          box-shadow: none;
}
.pg-index .separate-panel:after {
  content: '';
  display: block;
  position: absolute;
  top: -80px;
  width: 100%;
  height: 250px;
  background: #fff;
  -webkit-box-shadow: 0 -4px 30px 0 #DFE1E6;
          box-shadow: 0 -4px 30px 0 #DFE1E6;
  -webkit-transform: skewY(-4deg);
          transform: skewY(-4deg);
}

.demo-container {
  -webkit-transition: max-height 0.3s ease;
  transition: max-height 0.3s ease;
  overflow: hidden;
}
.docs-demo {
  width: 100%;
  height: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 14px;
  background-color: #F7F7F7;
  border: 1px solid #e2ecf4;
  border-top: none;
}
.docs-demo pre code {
  font-family: Consolas, Menlo, Courier, monospace;
  line-height: 22px;
  border: none;
}
.docs-trans {
  width: 100%;
  text-align: center;
  display: inline-block;
  color: #C5D9E8;
  font-size: 12px;
  padding: 10px 0;
  background-color: #FAFBFC;
}
.docs-demo__code,
.highlight-wrapper,
.docs-demo__meta {
  padding: 0 20px;
  overflow-y: auto;
}
.docs-demo__code {
  border-bottom: 1px solid #eee;
}
.docs-demo.docs-demo--expand .docs-demo__meta {
  border-bottom: 1px dashed #e9e9e9;
}
.docs-demo.docs-demo--expand .docs-demo__triangle {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.highlight-wrapper {
  display: none;
}
.highlight-wrapper p,
.highlight-wrapper pre {
  margin: 0;
}
.highlight-wrapper .hljs {
  padding: 0;
}
.docs-demo.docs-demo--expand .highlight-wrapper {
  display: block;
}
.docs-demo__code__mobi {
  height: 620px;
  margin: 20px 0;
}
.docs-demo__code__mobi__header {
  border-radius: 4px 4px 0 0;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(55, 55, 55, 0.98)), to(#545456));
  background: linear-gradient(rgba(55, 55, 55, 0.98), #545456);
  text-align: center;
  padding: 8px;
}
.docs-demo__code__mobi__header img {
  width: 100%;
}
.docs-demo__code__mobi__header .url-box {
  height: 28px;
  line-height: 28px;
  color: #fff;
  padding: 0 3px;
  background-color: #a2a2a2;
  margin: 10px auto 0;
  border-radius: 4px;
  white-space: nowrap;
  overflow-x: auto;
}
.docs-demo__code__mobi__content iframe {
  width: 100%;
  border: 0;
  height: 548px;
}
